import Vue from 'vue'
import VueRouter from 'vue-router'
/* 
import dashboard from '../views/Dashboard.vue'
import powerSystem from '../views/PowerSystemView.vue'
import thermostat from '../views/ThermostatView.vue'
import temperatures from '../views/TemperaturesView.vue'
import hvac from '../views/HVACView.vue'
import closure from '../views/ClosureView.vue'
import events from '../views/EventsView.vue'
import alerts from '../views/AlertsView.vue'
 */

Vue.use(VueRouter)

function lazyLoad(view){
  return() => import(`@/views/${view}.vue`)
}

const routes = [
  {
    path: '/',
    name: 'Overview',
    component: lazyLoad('Dashboard'),
  },
  {
    path: '/power_system',
    name: 'Power System',
    component: lazyLoad('PowerSystemView')
  },
  {
    path: '/thermostat',
    name: 'Thermostat',
    component: lazyLoad('ThermostatView')

  },
  {
    path: '/temperatures',
    name: 'Temperature Sensors',
    component: lazyLoad('TemperaturesView')

  },
  {
    path: '/hvac',
    name: 'HVAC',
    component: lazyLoad('HVACView')

  },
  {
    path: '/closures',
    name: 'Closures',
    component: lazyLoad('ClosureView')

  },
  {
    path: '/alerts',
    name: 'Alerts',
    component: lazyLoad('AlertsView')

  },
  {
    path: '/events',
    name: 'Events',
    component: lazyLoad('EventsView')

  },
 /* {
    path: '/settings',
    name: 'Settings',
    component: SettingsView

  } */
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
