<template> 
<nav  :class="{'mb-2':$vuetify.breakpoint.mobile}">
  <v-app-bar  app dark height="75" v-if="$vuetify.breakpoint.mobile" hide-on-scroll>
    <v-container>
        <v-layout row class="d-flex justify-center">
          <v-toolbar-title class="mb-n8 pt-4">{{$view.facility.name}} NSITE (beta)</v-toolbar-title>
        </v-layout>
        <v-layout row class="d-flex justify-space-between mt-n4">
          <v-img max-width="75" :src="require('@/assets/NEP-Logo.png')" class="mt-3"></v-img>
          <v-spacer></v-spacer>       
          <v-spacer></v-spacer>
          <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>
        </v-layout>
        <v-layout row class="d-flex justify-center overline mt-n4">
          <v-spacer></v-spacer><span>{{currentRouteName}}</span><v-spacer></v-spacer>
        </v-layout>
        <v-layout row class="d-flex justify-left mt-n4 " :class="classGoBack" >
          <v-icon v-show="goBackCount > 0" @click="goBack()">{{$icon.mdiKeyboardBackspace}}</v-icon>
          <v-spacer></v-spacer>
        </v-layout>
    </v-container>
  </v-app-bar>
  <v-navigation-drawer id="drawerWidth" height="100%" app v-model = "drawer" :expand-on-hover='expandOnHover' dark :key="expandOnHover">
    <!-- <div  class="d-flex"><v-spacer></v-spacer><v-btn style="z-index:10" icon @click="colapsDrawer"><v-icon class="mdi-rotate-45" color="#838383">{{drawerChevron}}</v-icon></v-btn></div> --> 
    
    <v-list-item class="" >
      <v-list-item-content>
        <v-list-item-title class="title">
          <div class='d-flex'>
            <v-spacer></v-spacer>
            <v-img  contain max-height="100" max-width="100"  :src="require('@/assets/NEP-Logo.png')"/>
            <!-- <v-img  v-if="!changeLogo" contain max-height="100" max-width="100"  :src="require('@/assets/NEP-Logo.png')"/>
            <v-img class="mt-4" v-else contain max-height="" max-width="24"  :src="require('@/assets/NEP-Logo-WHITE-N.png')"/> -->
            <v-spacer></v-spacer>
          </div>
        </v-list-item-title>
        <v-list-item-subtitle class="pt-1 text-center">
          <span>{{$view.facility.name}} NSITE (beta)</span>
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <v-divider></v-divider>
    <v-list dense :class="['', { 'px-2' : !expandOnHover }]">
      <v-list-item v-for="item in items" :key="item.title" router :to="item.route" link>
          <v-list-item-icon><v-icon>{{ item.icon }}</v-icon></v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.text }}</v-list-item-title>
          </v-list-item-content>
      </v-list-item>
    </v-list>
    <!-- <v-divider></v-divider> -->

      <v-footer absolute outlined color="transparent" class="pl-2" >
      <v-spacer v-if="!expandOnHover"></v-spacer><v-btn  icon @click="colapsDrawer"><v-icon  color="#838383">{{drawerChevron}}</v-icon></v-btn>
      <!-- <v-btn icon><v-icon>mdi-cog</v-icon></v-btn>
      <v-btn icon><v-icon>mdi-bell-alert</v-icon></v-btn> -->
    </v-footer> 
    
    <!-- <v-bottom-navigation background-color="transparent" absolute >
        <v-btn icon ><span>Settings</span><v-icon>mdi-cog</v-icon></v-btn>
      <v-btn icon><span>Events</span><v-icon>mdi-bell-alert</v-icon></v-btn>
    </v-bottom-navigation> -->
  </v-navigation-drawer>
</nav>
</template>

<script>
export default {
  props:[''],
  data() {
    return {
      drawerIMG:"https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg",
      drawer: false,
      items: [
        { icon: this.$icon.mdiViewDashboard, text: 'Overview', route: '/'},
        { icon: this.$icon.mdiFlash , text: 'Power System', route: '/power_system'},
        { icon: this.$icon.mdiThermostatBox, text: 'Thermostat', route: '/thermostat'},
        { icon: this.$icon.mdiTemperatureFahrenheit, text: 'Temperatures', route: '/temperatures'},
        { icon: this.$icon.mdiAirConditioner, text: 'HVAC', route: '/hvac' },
        { icon: this.$icon.mdiArrowSplitHorizontal, text: 'Closure', route: '/closures'},
        { icon: this.$icon.mdiAlert, text: 'Alarms', route: '/alerts'},
        { icon: this.$icon.mdiAccessPoint, text: 'Events', route: '/events'},
       // { icon: this.$icon.mdiCog, text: 'Settings', route: '/settings' },
      ],
      expandOnHover:false,
      showGoback:false,
      goBackCount:0,
      lastRouteName:'',
      drawerTransition:0
    }
  },
  created (){
    this.drawer = !this.$vuetify.breakpoint.mobile
    this.lastRouteName = this.$route.name
    if(localStorage.getItem('drawerPosition') === 'true'){
      this.expandOnHover = true
    }
    /* // check for thermostat
    if(!this.$view.thermostatMode && !this.$view.thermostatAvgTemp && !this.$view.thermostatSetTemp){
      this.$delete(this.items, 2)
    }
    // check for hvac
    if(!this.$view.hvac){
      this.$delete(this.items, 4)
    } */
  },
  computed:{
    currentRouteName() {
        return this.$route.name;
    },
    classGoBack(){
      return {
        'gobackPaddingTop0': this.goBackCount > 0,
        'gobackPaddingTop24': this.goBackCount <= 0
      }
    },
    drawerChevron(){
      if(this.expandOnHover){
        return this.$icon.mdiPinOutline
      } else {
        return this.$icon.mdiChevronLeft
      }
    },
    changeLogo(){
      return this.drawerTransition % 2
    }
    
  },
  watch:{
    '$route.name': function(){
       this.goBackCount = this.goBackCount + 1
    }
  },
  methods:{
    goBack(){
      this.goBackCount = this.goBackCount - 2
      return this.$router.go(-1)
    },
    test(){
      this.drawerTransition ++
      console.log(this.drawerTransition)
      /* if(this.expandOnHover){

      } */

    },
    test2(){
      console.log("mini")
    },
    colapsDrawer(){
      
      this.expandOnHover = !this.expandOnHover
      localStorage.setItem('drawerPosition',this.expandOnHover)
      
    }
  },
  
}
</script>

<style scoped>
.v-list-item--active{
  border-left-style: solid;
  border-left-width: thick;
  border-left-color: #0093D8;
 
}
.gobackPaddingTop24{
  padding-top: 24px;
}
.gobackPaddingTop0{
  padding-top: 0px;
}
.bt{
  color: black;
  border-color:rgba(255, 255, 255, 0.12)  !important;
  border-top:1px solid red !important
}

</style>>
