<template>
  <v-app>
    <Navigation v-if='viewLoaded'/> 
    <v-main>
      <v-container fluid :class="['', { 'pa-0' : $vuetify.breakpoint.mobile }]">
        <router-view v-if='viewLoaded'></router-view>
      </v-container>
      <v-overlay :value="overlay" opacity=".7">
        <div class="d-flex">
          <v-spacer></v-spacer>
          <v-progress-circular indeterminate size="64"></v-progress-circular>
          <v-spacer></v-spacer>
        </div>
        <div class="d-flex mt-2">
          <v-spacer></v-spacer>
          <span class="font-weight-bold headline">{{overlayPriText}}</span>
          <v-spacer></v-spacer>
        </div>
        <div class="d-flex">
          <v-spacer></v-spacer>
          <span class="title"> {{overlaySecText}}</span>
          <v-spacer></v-spacer>
        </div>
      </v-overlay>
    </v-main>
  </v-app>
</template>

<script>
import Navigation from './components/Navigation'
//import mqttEvent from './mqttEvent'
import mqtt from 'mqtt'
import address from './server'

export default {
  name: 'App',
  components: {
    Navigation,
  },
  data () {
    return {
      v:{},
      users:{},
      testView:{},
      viewLoaded:false,
      overlay:true,
      overlayPriText:'',
      overlaySecText:''
    }
  },
  methods:{
    connectToBroker(facility){
      var client = mqtt.connect(`ws://${address}:80`,{
        clientId:`${facility.name} web app - ${new Date().getTime()}`
      })
      client.on('connect', () => {
        client.subscribe(`${facility.name}/readings`, () => {
          //console.log(`subscribed to readings`)
          this.serverConnected(true)
        })
      })
      client.on('message', (topic, payload) => {
        let msg = payload.toString() 
        let readings = JSON.parse(msg)
        this.handleMessage(readings)
        //console.log(readings)
      })
      client.on('close', () => {
        this.serverConnected(false)
      })
    },
    serverConnected(con){
      if(con){
        this.overlay = false
      } else if (!con){
        this.overlayPriText = 'Disconnected From Server'
        this.overlaySecText = 'Attempting to Reconnect'
        this.overlay = true
      }
    },
    containsKey: function (obj, key) {
      if(Object.keys(obj).includes(key)){
        return true
      } else {
        return false
      }
    },
    handleMessage(msg){
      var vm = this
      Object.keys(vm.$view).forEach(x => {
        Object.keys(msg).forEach(y => {
          if(vm.containsKey(vm.$view[x],y)){
          vm.$view[x][y].value = msg[y].value
          /* if(Object.prototype.hasOwnProperty.call(readings[y], 'ticker')){
            vm.$view[x][y].ticker = readings[y].ticker
            //this.$set(vm.view[x][y].ticker,[y],readings[y].ticker)
          } */
          //console.log(vm.view[x])
          }
        })
      })
      Object.keys(msg).forEach(x => {
        //this.$set(this.$readings,x,msg[x])
        this.$set(this.$root.readingsTest,x,msg[x]) //<----------
        if(!this.$readingsIDs.includes(x)){
          this.$readingsIDs.push(x)
        }
      })
      Object.keys(msg).forEach(y => {
        this.$set(this.$tags,y,msg[y])
      })
      this.viewLoaded = true
    },
  },
  created(){
     //this.loadUsers('/api/view')
     this.$http.get('/api/view').then(res => {
        this.connectToBroker(res.facility);
        Object.keys(res).forEach(x => {
          this.$set(this.$view,x,res[x])
        })
     })
      
     
    //let vm = this
    /* this.$http.get('/api/view',function(err,ret){
      vm.v = ret.data
      //this.connectToBroker('test')
      Object.keys(vm.v).forEach(x => {
        vm.$set(vm.$view,x,vm.v[x])
      })
      vm.viewLoaded = true
      
    }) */
    
     
    //console.log("$json",this.$view.facility.name)
    
    //console.log(this.$viewTest)
    //mqttEvent.$on('message',this.handleMessage)
    //mqttEvent.$on('connected',this.serverConnected)
  },
  mounted (){
    
  },
  computed:{
    viewAgain(){
      return this.$view
    }
  }
};
</script>

<style scoped>
.container{
  padding: 8px;
} 
</style>
