export const api = {
    get: async (sub) => {
        const response = await axios.get(`${httpURL}${sub}`)
        return response.data
    },
    getEmail: async (sub) => {
        const response = await axios.get(`${betaURL}${sub}`)
        return response.data
    },
    post: async (sub,data) => {
        const response = await axios.post(`${httpURL}${sub}`,data,axiosConfig)
        return response.data
    },
    postEmail: async (sub,data) => {
        const response = await axios.post(`${betaURL}${sub}`,data,axiosConfig)
        return response.data
    }
} 




