Vue.prototype.$rules = {
  required:[v => !!v || "Required."], 
  noSpaces:[v => (v || '').indexOf(' ') < 0 ||'No spaces are allowed'],
  number:[v => !isNaN(parseFloat(v)) ||'Must be a number'],
  numberOrBeEmpty:[v => (v && v.length > 0 && isNaN(parseFloat(v)))? 'Must be a number':true],
  email: [ v => !v || /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'E-mail must be valid']
}

Vue.prototype.$comparison = {
  equal:              function(a, b) { return a === b },
  notEqual:           function(a, b) { return a != b },
  lessThan:           function(a, b) { return a < b },
  lessThanOrEqual:    function(a, b) { return a <= b },
  graterThan:         function(a, b) { return a > b },
  graterThanOrEqual:  function(a, b) { return a >= b },
  between:function(a, v){
      a = a.split(/[!,?,.]/)
      let array = a.map(Number)
      if(v > array[0] && v < array[1]){
          return true
      } else {
          return false
      }
  },                          
  notBetween:function(a, v){
      a = a.split(/[!,?,.]/)
      let array = a.map(Number)
      if(v > array[0] && v < array[1]){
          return false
      } else {
          return true
      } 
  } 
}

